<template>
<div id="authorityMgmt">
	<!-- 角色管理 -->
	<div class="roles">
		<div class="selectCheck">
			<select v-model="role" style="width:8rem;">
				<option disabled>基础角色选择</option>
		    	<option v-for="item in selRoleList" :value="item.value">{{ item.text }}</option>
		    </select>
			<input v-model="userIdOrName" placeholder="输入姓名或编码" style="width:6rem;" @change="inputUser()">
		</div>
		<!-- 选择基本角色后，从后台返回角色列表 -->
		<table class="roleTable">
			<tbody>
				<tr v-for="person in userList">
					<td>
						<Checkbox v-model="person.userId" :checked="person.checked" @change="checkUser()"></Checkbox>
					</td>
					<td :value="person.userName">{{person.userName}}</td>
					<td :value="person.userId">{{person.userId}}</td>
				</tr>
			</tbody>
		</table>
	</div>
	<div class="authorities">
		<div class="changed">
			<span>当前选择：{{userName}}</span>
			<!-- <button class="am-btn am-btn-su-ccess am-radius" @click="changeBtn">修改权限</button> -->
		</div>
		<div class="changedTable">
			<table>
				<tbody>
					<tr v-for="(authority, index) in roleList" :key="authority">
						<td>
							<Checkbox v-model="authorityRoleId" :value="authority.roleId"></Checkbox>
						</td>
						<td :value="authority.roleName">{{authority.roleName}}</td>
					</tr>
				</tbody>
			</table>
		</div>
		<div class="changedBtn">
			<button class="am-btn am-btn-success am-radius" @click="saveBtn()">保存</button>
			<button class="am-btn am-btn-success am-radius" @click="cancelBtn()">取消</button><!-- 尚未设定功能 -->
		</div>
	</div>


</div>
</template>

<script>
export default {
	data () {
		return {
			// 选择角色
			role: '基础角色选择',
			selRoleList: [
				{text: '教师', value: '2'},
				{text: '管理员', value: '3'}
			],
			userIdOrName: '',	//输入姓名或编码
			userName: '',
			userList: [		// 角色下的人
				{userName: '李华', userId: '20142201', checked: false},
				{userName: '李华', userId: '20142201', checked: false}
			],
			roleList: [		// 一人的全部角色数据
				// {roleName: '教师', roleId: '1'},
				// {roleName: '督导', roleId: '2'},
				// {roleName: '自定义角色', roleId: '3'}
			],
			authorityRoleId: []
		}
	},
	beforeMount: function() {
        this.$http.post('./getAllTeacher',{},{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log("获取申请:");
            console.log(response.body);
            var data = response.body;
            this.userList = data.allteacherList;
            this.roleList = data.allroleList;
        },function(error){
            console.log("获取申请error:");
            console.log(error);
        });
    },
	methods: {
		// 下拉框-基础角色选择
		selRoleListClick: function () {
			this.$http.post('./getAllRole',{
	        	"roleId": this.role
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log("获取申请:");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.userList = data.userList;
	            }else{
			        alert("操作失败！请重试");
			    }
	        },function(error){
	            console.log("获取申请error:");
	            console.log(error);
        	});
		},
		// 输入输入姓名或编码，查找用户
		inputUser: function () {
			this.$http.post('./getUserByIdOrName',{
	        	"userIdOrName": this.userIdOrName
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log("获取申请:");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.userList = data.userList;
	            }else{
			        alert("操作失败！请重试");
			    }
	        },function(error){
	            console.log("获取申请error:");
	            console.log(error);
        	});
		},
		// 选择用户，显示用户的所有角色
		checkUser: function () {
			if (this.checked == true) {
				this.$http.post('./getUserRole',{
		        	"userId": this.userId
		        },{    
		            "Content-Type":"application/json"
		        }).then(function(response){
		            console.log("获取申请:");
		            console.log(response.body);
		            var data = response.body;
	            	this.userName = data.userName;
	            	this.authorityRoleId = data.roleId;
		        },function(error){
		            console.log("获取申请error:");
		            console.log(error);
	        	});
			}else{
				this.$http.post('./getUserRole',{},{    
		            "Content-Type":"application/json"
		        }).then(function(response){
		            console.log("获取申请:");
		            console.log(response.body);
		            var data = response.body;
	            	this.authorityRoleId = [];
		        },function(error){
		            console.log("获取申请error:");
		            console.log(error);
	        	});
			}
		},
		// 保存角色设定
		saveBtn: function () {
			this.$http.post('./setUserRole',{
	        	"userId": this.userId,
	        	"roleId": this.authorityRoleId
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log("获取申请:");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	alert("保存成功！");
	            }else{
			        alert("操作失败！请重试");
			    }
	        },function(error){
	            console.log("获取申请error:");
	            console.log(error);
        	});
		},
		// 取消角色修改
		cancelBtn: function () {
			this.$http.post('./getUserRole',{},{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log("获取申请:");
	            console.log(response.body);
	            var data = response.body;
            	this.authorityRoleId = data.roleId;
	        },function(error){
	            console.log("获取申请error:");
	            console.log(error);
        	});
		}
	}
}
</script>

<style>
#authorityMgmt {
	padding: 3.5rem 5rem;
    display: flex;
    justify-content: space-between;
}
.roles {
	width: 20rem;
	height: 30em;
	border: 1px solid black;
	padding: 1rem 1rem;
	/*margin-right: 4.5rem;*/
}
.selectCheck {
	padding: 0.5rem;
	border-bottom: 2px solid #158064;
}
.selectCheck select,
.selectCheck input {
	margin: 0 0.7rem;
}
.roleTable {
  width: 100%;
  border-spacing: 0;
  border-collapse: separate;
  border: none;
}
.roleTable > tbody > tr > th,
.roleTable > tbody > tr > td {
  padding: 0.4rem 0rem;
  line-height: 1.6;
}
/*.roleTable th {
  font-size: 0.9rem;
  font-weight: bold;
  background-color: white;
  height: 2rem;
}*/
.roleTable td {
  font-size: 0.5rem;
  height: 2rem;
  text-align: center;
}
/*.roleTable select {
	text-align: center;
	width: 80%;
	height: 1.8rem;
	font-size: 0.8rem;
	outline: none;
	cursor: pointer;
}*/

.authorities {
	width: 40rem;
	height: 30em;
	border: 1px solid black;
	padding: 1rem 1rem;
	/*margin-left: 4.5rem;*/
}
.changed {
	padding: 0.6rem;
	line-height: 1.6;
	border-bottom: 2px solid #158064;
}
.changedTable {
	height: 20rem;
}
.changedTable td {
	padding-right: 1rem;
}
.changedBtn {
	display: flex;
 	justify-content: center;
}
.changedBtn button {
	margin:  0 0.7rem;
	/*width: 5.6rem;*/
}

@media screen and (max-width:1023px){
  html{
    font-size: 56%;
  }
}
</style>


